.App {
 margin: 0 0 0 0;
 padding: 0;
}

.hello-text {
  margin: 65vh 0% 0% 10vw;
}

.hello-text h1{
  font-size: 3em;
  position: relative;
}

.hello-text h1::after{
  content: "";
  position: absolute;
  width: 5.5em;
  background: yellow;
  left: .4em;
  bottom: -0em;
  height: .6em;
  z-index: -1;
}


.hello-text p{
  margin: 0 0 0 5%;
  font-family: 'Amiri', 'serif';
  font-size: 2em;
  width: 31%;
  line-height: 1em;
}


.hello-button {
  margin: 0 0 0 5%;
}



